body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.App {
  text-align: center;
  background: #FEE433;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column; 
  height: 100vh;
}

.container{
  max-width: 480px;
}

.loader{
  border-radius: 120px;
  width: 250px;
  height: 250px;
}

.weather-text{
  font-size: 1.5rem;
}

.weather-Icon{
  font-size: 8rem;
  margin-top: 15px;
}

.weather-description{
  margin: 15px 0px 0px 0px;
  font-size: 2rem;
  

}

.weather-temp{
  margin: 5px 0px 0px 0px;
  font-size: 1.5rem;
  
}

.credits-container{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content:right;
          justify-content:right;
  -webkit-align-items: center;
          align-items: center;
  height: 5vh;
  width: 100%;
  background: #FEE433;
  font-size:8px;
  color:black;
}

